@import "./animation.scss";

.appStore-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0px 60px;
}
.appStore-text-container {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 32px;
	width: 540px;

	.appStore-p-wrapper {
		width: auto;
	}
}

.appStore-mockup-container {
	width: 660px;
	height: 497px;

	align-items: center;
	border-radius: 12px;
	border: 6px solid rgba(0, 0, 0, 0.1);
	overflow: hidden;
	justify-content: flex-start;
}

.appStore-mockup {
	background-image: url("../../../Assets/AppStore/appStore-mockup.png");
	background-size: auto;
	background-repeat: no-repeat;
	width: 660px;
	height: 2048px;
	animation: 40s ease-in-out scroll-stripe-appStore infinite;

	&:hover {
		animation-play-state: paused;
	}
}

.appStore-buttons-container {
	display: flex;
	flex-direction: row;
	gap: 16px;
}
@media only screen and (max-width: 1435px) {
	.appStore-text-container {
		width: 45%;
	}

	.appStore-mockup-container {
		width: 45%;
		height: 350px;
	}
	.appStore-mockup {
		background-size: contain;
		width: 100%;
	}
}

@media only screen and (max-width: 1050px) {
	.appStore-text-container {
		width: 100%;
	}
	.appStore-container {
		flex-direction: column;
		gap: 48px;
		padding: 0px 32px;
	}
	.appStore-mockup-container {
		width: 100%;
		height: 500px;
	}
	.appStore-mockup {
		background-size: cover;
	}
}

@media only screen and (max-width: 680px) {
	.appStore-container {
		padding: 0px 16px;
	}
	.appStore-buttons-container {
		display: flex;
		flex-direction: column;
		gap: 16px;
	}

	.appStore-mockup-container {
		height: 300px;
		width: 100%;
	}
	.appStore-mockup {
		width: 100%;
		background-size: contain;
	}
}
